@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

h1 {
    font-family: MapoFlowerIsland;
}

@font-face {
    font-family: 'MapoFlowerIsland';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/MapoFlowerIslandA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
.next {
    display: inline-block;
    border-radius: 4px;
    background-color: #f4511e;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 20px;
    padding: 10px;
    width: 100px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
    font-family: MapoFlowerIsland;
}

.next span {
cursor: pointer;
display: inline-block;
position: relative;
transition: 0.5s;
}

.next span:after {
content: '\00bb';
position: absolute;
opacity: 0;
top: 0;
right: -20px;
transition: 0.5s;
}

.next:hover span {
padding-right: 25px;
}

.next:hover span:after {
opacity: 1;
right: 0;
}

div {
    font-family: MapoFlowerIsland;
}

.result {
    font-size: 20px;
    padding: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
    border: 1px solid black;
    border-radius: 30px;
}

.right {
    margin-right: 10px;
}

h3 {
    font-size: 30px;
}
p {
    font-size: 20px;
}
section {
    height: 100%;
    justify-content: center;
    text-align: center;
}

h1 {
    font-family: MapoFlowerIsland;
}

@font-face {
    font-family: 'MapoFlowerIsland';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/MapoFlowerIslandA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.search {
    font-family: MapoFlowerIsland;
}

.searchBar {
    white-space: nowrap;
    position: relative;
}

input {
    border-radius: 10px;
    font-size: 20px;
    font-family: MapoFlowerIsland;
    padding-left: 30px;
}

.icon {
    position: absolute;
    top: 16%;
    margin-left: 8px;
    z-index: 1;
    color: #4f5b66;
}
div {
    height: 100%;
    justify-content: center;
    text-align: center;
}

table {
    border-radius: 5px;
    font-size: 15px;
    font-weight: normal;
    border: none;
    border-collapse: collapse;
    width: 80%;
    max-width: 80%;
    white-space: nowrap;
    background-color: white;

    margin: 5% 10% 10%;
    box-shadow: 0px 35px 50px rgba( 0, 0, 0, 0.2 );
}

td, th {
    text-align: center;
    padding: 8px;
}

th {
    margin: 0px;
}

td {
    border-right: 1px solid #f8f8f8;
    font-size: 15px;
}

thead th{
    color: #ffffff;
    background: #4FC3A1;
}
thead th:nth-child(odd) {
    color: #ffffff;
    background: #324960;
}
tr:nth-child(even) {
    background: #f8f8f8;
}
.nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

.menu {
    float: left;
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-family: MapoFlowerIsland;
    font-size: 20px;
}

.menu:hover {
    background-color: #111;
}

@font-face {
    font-family: 'MapoFlowerIsland';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/MapoFlowerIslandA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
